import React, { Component, Fragment } from "react";

import Header from "../../Components/Header/Header.js";
import Footer from "../../Components/Footer/Footer";
import SocialMedias from "../../Components/SocialMedias/SocialMedias";

import "./Login.scss";

class Login extends Component {
  state = {
    mail: "",
    password: ""
  };

  callApi = async (url, corps) => {
    const response = await fetch(url, corps);
    const body = await response.json();
    if (response.status !== 200) throw Error(body.message);
    return body;
  };

  handleChange(evt) {
    console.log(evt.target.value);
    this.setState({ [evt.target.name]: evt.target.value });
  }

  loginUser(evt) {
    evt.preventDefault();
    const data = { mail: this.state.mail, password: this.state.password };
    this.callApi(`/user`, { method: "POST", body: JSON.stringify(data) })
      .then(response => {
        // this.forceUpdate();
        console.log(response);
        // window.location.reload();
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <Fragment>
        <Header />
        <div className="loginPageWrap">
          <h1>CONNEXION</h1>
          <section className="loginWrap">
            <div className="loginMiniWrap">
              <h2>Déjà inscrit-e ?</h2>
              <form onSubmit={e => this.loginUser(e)}>
                <input
                  type="text"
                  placeholder="email"
                  name="mail"
                  onChange={e => this.handleChange(e)}
                />
                <input
                  type="text"
                  name="password"
                  placeholder="mot de passe"
                  onChange={e => this.handleChange(e)}
                />
                <input type="submit" />
              </form>
            </div>
            <div className="loginMiniWrap">
              <h2>Créez votre compte</h2>
            </div>
          </section>
        </div>
        <Footer />
        <SocialMedias />
      </Fragment>
    );
  }
}

export default Login;
